.resume-section {
  padding: 60px 0;
  min-height: 100vh;
  background-color: #f9fafb;
}

body.dark-mode .resume-section {
  background-color: #111827;
}

.resume-section .container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}

.resume-content {
  max-width: 900px;
  margin: 0 auto;
}

.resume-header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.section-title {
  font-size: 2.2rem;
  font-weight: 700;
  margin: 0;
  text-align: left;
}

.btn-download {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: var(--color-primary);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.btn-download:hover {
  background-color: var(--color-primary-dark);
}

.resume-paper {
  background-color: white;
  padding: 2.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  color: #333;
  line-height: 1.6;
  max-width: 850px;
  margin: 0 auto;
}

body.dark-mode .resume-paper {
  background-color: #1f2937;
  color: #e5e7eb;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.resume-header {
  margin-bottom: 1.5rem;
  text-align: center;
}

.resume-name {
  font-size: 1.8rem;
  margin: 0 0 0.5rem;
  letter-spacing: 1px;
  font-weight: 700;
  color: #333;
  text-transform: uppercase;
}

body.dark-mode .resume-name {
  color: #f9fafb;
}

.resume-contact-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
  color: #555;
  margin-top: 0.5rem;
}

.separator {
  color: #999;
  font-weight: bold;
}

.resume-summary {
  margin-bottom: 2rem;
  text-align: left;
  line-height: 1.5;
  font-size: 0.95rem;
  color: #333;
}

.section-heading {
  font-size: 1rem;
  font-weight: 700;
  margin: 2rem 0 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #000;
  border-top: 1px solid #000;
  color: #333;
  letter-spacing: 1px;
  text-align: center;
  text-transform: uppercase;
  padding: 0.5rem 0;
}

body.dark-mode .section-heading {
  color: #f3f4f6;
  border-bottom-color: #374151;
  border-top-color: #374151;
}

.experience-item, .education-item {
  margin-bottom: 1.5rem;
  position: relative;
}

/* Experience Items */
.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.job-title-company {
  text-align: left;
}

.job-title-company h3 {
  font-size: 1rem;
  font-weight: 700;
  margin: 0 0 0.25rem 0;
  color: #333;
}

.company {
  font-size: 0.9rem;
  font-style: italic;
  color: #555;
  display: block;
}

.job-date {
  font-size: 0.9rem;
  color: #555;
  text-align: right;
  white-space: nowrap;
}

.job-bullets {
  margin: 0.5rem 0 0 1.25rem;
  padding: 0;
  text-align: left;
}

.job-bullets li {
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  line-height: 1.4;
  position: relative;
  padding-left: 0.25rem;
}

body.dark-mode .job-title-company h3 {
  color: #f3f4f6;
}

body.dark-mode .company,
body.dark-mode .job-date {
  color: #9ca3af;
}

.education-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.degree-institution {
  text-align: left;
}

.degree-institution h3 {
  font-size: 1rem;
  font-weight: 700;
  margin: 0 0 0.25rem 0;
  color: #333;
}

.institution {
  font-size: 0.9rem;
  font-style: italic;
  color: #555;
  display: block;
}

.education-date {
  font-size: 0.9rem;
  color: #555;
  text-align: right;
  white-space: nowrap;
}

.education-details {
  margin: 0.5rem 0 0 1.25rem;
  padding: 0;
  text-align: left;
}

.education-details li {
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  line-height: 1.4;
}

body.dark-mode .degree-institution h3 {
  color: #f3f4f6;
}

body.dark-mode .institution,
body.dark-mode .education-date {
  color: #9ca3af;
}

/* Skills section */
.skills-content {
  text-align: left;
  margin-top: 1rem;
}

.skills-paragraph {
  margin-bottom: 1rem;
  font-size: 0.9rem;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .resume-paper {
    padding: 1.5rem;
  }
  
  .experience-header, .education-header {
    flex-direction: column;
  }
  
  .job-date, .education-date {
    margin-top: 0.25rem;
    text-align: left;
  }
  
  .resume-contact-row {
    flex-direction: column;
    align-items: center;
  }
  
  .separator {
    display: none;
  }
}

.resume-item li {
  margin-bottom: 6px;
  line-height: 1.5;
  text-align: left;
}

/* Two Column Layout */
.resume-two-column {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-top: 15px;
}

/* Skills Styling */
.skills-category {
  margin-bottom: 12px;
}

.skills-category h4 {
  font-size: 1rem;
  font-weight: 600;
  margin: 0 0 10px 0;
  color: #4b5563;
}

body.dark-mode .skills-category h4 {
  color: #d1d5db;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.skills-list span {
  background-color: #eef2ff;
  color: #4f46e5;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 0.85rem;
  font-weight: 500;
}

body.dark-mode .skills-list span {
  background-color: #312e81;
  color: #a5b4fc;
}

.language-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.language-list li {
  margin-bottom: 8px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .resume-header {
    flex-direction: column;
    gap: 15px;
  }
  
  .resume-contact-info {
    text-align: left;
  }
  
  .resume-contact-info p {
    justify-content: flex-start;
  }
  
  .resume-two-column {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .resume-paper {
    padding: 25px;
  }
  
  .resume-header-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
}

@media (max-width: 480px) {
  .resume-name-title h1 {
    font-size: 2rem;
  }
  
  .resume-section h3 {
    font-size: 1.2rem;
  }
  
  .job-title-company h4, .education-degree h4 {
    font-size: 1rem;
  }
}

/* Dark mode styles */
body.dark-mode .resume-paper {
  background-color: #1f2937;
  color: #e5e7eb;
}

body.dark-mode .resume-header {
  border-bottom-color: #374151;
}

body.dark-mode .resume-header h1,
body.dark-mode .resume-section h3 {
  color: #f3f4f6;
}

body.dark-mode .resume-section h3 {
  border-bottom-color: #374151;
}

body.dark-mode .resume-item-header h4 {
  color: #60a5fa;
}

body.dark-mode .resume-item-details {
  color: #d1d5db;
}

body.dark-mode .resume-item-details .date,
body.dark-mode .resume-item-details .location {
  color: #9ca3af;
}

body.dark-mode .section-subtitle {
  color: #9ca3af;
}

body.dark-mode .skills-list span {
  background-color: #1e3a8a;
  color: #93c5fd;
}

/* Responsive styles */
@media (max-width: 768px) {
  .resume-section {
    padding: 60px 0;
  }
  
  .resume-paper {
    padding: 25px;
  }
  
  .resume-contact-info {
    flex-direction: column;
    gap: 10px;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .resume-header h1 {
    font-size: 1.8rem;
  }
}
